.footer{display:flex;flex-direction:row;justify-content:center;flex-wrap:wrap;background-color:#000}.footer a{color:#fff;cursor:pointer;padding:44px 66px 44px 66px;transition:background .5s}.footer a:hover{font-style:italic;background-color:dimgray}.footer a:hover::after{content:" />"}.footer a:hover::before{content:"< "}
