.modal-wrapper{position:absolute;background-color:rgba(0,0,0,0)}@-webkit-keyframes fadeInAnim{0%{opacity:0}100%{opacity:1}}@keyframes fadeInAnim{0%{opacity:0}100%{opacity:1}}.modal-background{width:100vw;min-height:100vh;height:100%;position:fixed;display:flex;flex-direction:column;justify-content:flex-start;align-items:center;overflow:scroll;-webkit-backdrop-filter:blur(16px);backdrop-filter:blur(16px);background:rgba(50,50,50,.6509803922);z-index:10}.modal{position:relative;-webkit-animation:fadeInAnim ease .5s;animation:fadeInAnim ease .5s;display:flex;flex-direction:column;justify-content:center;align-items:center;border-radius:0px 20px 0px 0px;background-color:#fff;box-shadow:0px 0px 30px -6px #000;max-width:900px;padding:26px;padding-top:0px;margin-top:100px;margin-bottom:100px}.modal .close-modal-button{position:absolute;color:#fff;background-color:dimgray;font-weight:bold;right:0;top:0;padding:10px 20px 10px 20px;border-radius:0px 15px 0px 10px}.modal .close-modal-button:hover{cursor:pointer;background-color:red}.modal .project-image{max-width:100%}.modal #modal-title{display:flex;justify-content:center;align-items:center;color:#000;padding:22px 16px 16px 16px;width:80%;font-size:20px;line-height:20px;font-weight:bold;margin-bottom:25px;border-bottom:1px dotted #000}.modal .tech-list{display:flex;flex-direction:row;justify-content:center;align-items:center;flex-wrap:wrap;margin-top:22px}.modal .tech-list>*{background-color:#f5f5f5;border-radius:1000px;padding:1px 10px 1px 10px;margin:0px 1px 6px 1px;font-size:16px}.modal .project-description{margin-top:16px;margin-bottom:26px}.modal .project-links{margin-bottom:26px}.modal .project-links>a{padding:10px;padding-right:0px}.modal .project-links>a:not(:last-child):after{content:"â€¢";padding-left:10px}.modal .highlights{margin-top:0px;width:100%}.modal .highlights>li{margin-bottom:12px}.modal .highlights a{font-weight:300}.modal .highlights a:hover{text-decoration:underline}
.slideshow{max-width:80vw}.slideshow>img,.slideshow>iframe{max-width:100%;width:100%;box-shadow:#000 0px 0px 39px -12px}.slideshow .thumbnails{display:flex;flex-direction:row;flex-wrap:nowrap;overflow-x:scroll;max-width:100%}.slideshow .thumbnails .thumbnail{width:100px;height:60px;padding:10px;object-fit:contain}.slideshow .thumbnails .thumbnail:hover,.slideshow .thumbnails .thumbnail.selected{background-color:#ccc;cursor:pointer}
