s:center}#projects-section .project-item-card .project-top-bar>span{white-space:nowrap}#projects-section .project-item-card .project-top-bar>span:not(:last-child){padding-right:2px}#projects-section .project-item-card .project-top-bar>span.tech-pill{background-color:#f5f5f5;color:dimgray;border-radius:1000px;padding:1px 10px 1px 10px;margin:0px 1px 0px 1px;font-size:16px}#projects-section .project-item-card .project-top-bar>span.project-name{font-size:16px;font-weight:bold;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;padding:5px;padding-right:20px;padding-left:0px}
